import React from 'react'
import './TechnicalSkills.css';
import { themeContext } from '../../Context';
import { useContext } from 'react';
const TechnicalSkills = () => {
   const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
    <div id='skills' >
          <br/>
            <span className='my-Tech' style={{color:"rgb(229, 76, 76)" }} > Technical Skills</span><br/>
            
            
           <br/> <br/>
            <div class="technicalSkillsList" >
        <div className='skills-card' style={{textAlign:"center",border:darkMode?"2px solid teal":""}} >
            <img className='skills-card-img' src="https://cdn-icons-png.flaticon.com/512/732/732212.png" alt="html" /> <br/>
            <span className='skills-card-name' >HTML</span>
        </div>
        <div className='skills-card' style={{textAlign:"center",border:darkMode?"2px solid teal":""}}>
            <img className='skills-card-img' src="https://upload.wikimedia.org/wikipedia/commons/thumb/6/62/CSS3_logo.svg/800px-CSS3_logo.svg.png" alt="css"  /> <br/>
            <span className='skills-card-name'>CSS</span>
        </div>
        <div className='skills-card' style={{textAlign:"center",border:darkMode?"2px solid teal":""}}>
            <img className='skills-card-img' src="https://www.freepnglogos.com/uploads/javascript-png/js-logo-png-5.png" alt="JS"  /> <br/>
            <span className='skills-card-name'>JavaScript</span>
        </div>
         <div className='skills-card' style={{textAlign:"center",border:darkMode?"2px solid teal":""}}>
            <img className='skills-card-img'  src="https://cdn.icon-icons.com/icons2/2415/PNG/96/react_original_logo_icon_146374.png" alt="React" /> <br/>
            <span className='skills-card-name'>React-Js</span>
        </div>
         <div className='skills-card' style={{textAlign:"center",border:darkMode?"2px solid teal":""}}>
            <img className='skills-card-img' src="https://www.rlogical.com/wp-content/uploads/2021/08/Rlogical-Blog-Images-thumbnail.png" alt="Next-Js" /> <br/>
            <span className='skills-card-name'>Next-Js</span>
        </div> 
        <div className='skills-card' style={{textAlign:"center",border:darkMode?"2px solid teal":""}}>
            <img className='skills-card-img' src="https://cdn.icon-icons.com/icons2/2415/PNG/512/typescript_original_logo_icon_146317.png" alt="TypeScript" /> <br/>
            <span class='skills-card-name'>TypeScript</span>
        </div> 
        <div className='skills-card' style={{textAlign:"center",border:darkMode?"2px solid teal":""}}>
            <img className='skills-card-img' src="https://user-images.githubusercontent.com/107903370/222921490-6961a54d-49b7-47d9-92b5-dfed0bcfcb33.png" alt="Node-Js"  /> <br/>
            <span className='skills-card-name'>Node-Js</span>
        </div>
        
        </div>
      
          
         
          
   

   
      
    </div>
  )
}

export default TechnicalSkills