import React from 'react'

import './Navbar.css';

import { Link } from 'react-scroll';
import { useState } from 'react';
import Resume from './Gedela_Sekhar_Resume.pdf' ;
import { Box } from '@chakra-ui/react';

const Navbar2 = () => {
  
    const [clicked,setClicked] = useState(true);
    const ResumeLink=()=>{
      window.open("https://drive.google.com/file/d/1svUmwMkA4aCWevipoy4p59P2rDlsJAYU/view?usp=sharing")
    }
  return (
    <Box className="n-wrapper"   id='nav-menu' >
        <div className="n-left">
            <div className="n-name">Chandra Sekhar</div>
    
        </div>
        <div className="n-right">
            <div>
                <ul id="n-list" className={clicked? "#n-list":"#n-list active"}  > 
                <Link spy={true}  className="nav-link home" to='home' smooth={true} activeClass='activeClass'>
                         <li>Home</li>   </Link>  
                    <Link spy={true} className="nav-link about" to= 'about' smooth={true} onClick={()=>{setClicked(!clicked)}} >
                         <li>About</li>  </Link>   
                    <Link spy={true} to= 'skills' className="nav-link skills" smooth={true} onClick={()=>{setClicked(!clicked)}} >
                    <li>Skills</li></Link>  
                    <Link spy={true} to='projects' className="nav-link projects" smooth={true} onClick={()=>{setClicked(!clicked)}} >
                    <li>Projects</li></Link>   
                    
                    <a href={Resume} id="resume-link-1" className="nav-link resume"  download  onClick={ResumeLink}  >  <button id="resume-button-1">Resume</button>  </a>
                    
                    <Link spy={true} to='contact' className="nav-link contact" smooth={true} onClick={()=>{setClicked(!clicked)}} >
                     <li>Contact</li></Link> 
                </ul>
              
            </div>
            
            
        </div>
        <div>
          

        <i id="bar" className={clicked?'fas fa-bars':'fas fa-times'} onClick={()=>{setClicked(!clicked)}} ></i> 
                </div>
      
    
      
    </Box>
  )
}

export default Navbar2